html {
  box-sizing: border-box;
  font-size: 100%;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  font-family: Inter, sans-serif;
  margin: 0;
  padding: 0;
  background-color: var(--background-color);
  color: var(--text-color);
}

:root h1 {
  font-weight: 500;
  margin: 0px;
}
:root h2 {
  font-size: 2.8rem;
  margin-bottom: 59px;
}
:root h3 {
  font-size: 1.68rem;
  font-weight: 800;
  margin: 0px;
}
:root h4 {
  font-weight: 600;
  font-size: 2rem;
  text-align: center;
}
:root p {
  font-weight: 500;
  line-height: 24px;
  margin: 0px;
}
:root a {
  cursor: pointer;
  text-decoration: none;
  color: var(--text);
}
:root strong {
  font-weight: 900;
}

@media (min-width: 20em) {
  h1 {
    font-size: 1.75rem;
  }
}
@media (min-width: 44.6em) {
  h1 {
    font-size: 2.5rem;
  }
}
@media (min-width: 62.3em) {
  h1 {
    font-size: 2.65rem;
  }
}
:root {
  --white: rgb(255, 255, 255);
  --black: hsl(0, 0%, 12%);
  --brown: hsl(29, 30%, 61%);
  --light-brown: hsl(29, 63%, 82%);
  --sage: hsl(138, 21%, 88%);
  --light-yellow: hsl(40, 43%, 96%);
  --yellow: #FFB802;
  --background-color: var(--white);
  --text-color: var(--black);
  --buttons: var(--white);
  --buttons-hover: var(--black);
}

@keyframes fadeIn {
  from {
    top: 0;
  }
  to {
    top: -10px;
  }
}
@keyframes wobble {
  0% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(20px);
  }
  100% {
    transform: translateY(0px);
  }
}
.button {
  display: inline-block;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  border-radius: 37px;
  background-color: var(--buttons);
  font-weight: 800;
  font-size: 1.25rem;
  padding: 14px 20px 14px 20px;
  transition: 0.5s;
}
.button:hover {
  position: relative;
  top: -10px;
  animation: fadeIn 0.5s;
  background-color: var(--buttons-hover);
  color: var(--white);
}

.socials {
  display: flex;
  gap: 14px;
}

h2 {
  text-align: center;
}

body {
  background-image: url(/public/assets/backgroundmobile.png);
  background-repeat: no-repeat;
  background-position: top;
  background-position-y: 60px;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--white);
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
  position: sticky;
  top: 0;
  padding: 22px;
}
header .mobile-nav-toggle {
  width: 40px;
  background: red;
  aspect-ratio: 1;
  z-index: 9000;
  background: url(/public/assets/menu.png);
  background-repeat: no-repeat;
  background-size: contain;
  border: 0;
}
header .mobile-nav-toggle:hover {
  cursor: pointer;
}
header .mobile-nav-toggle[aria-expanded=true] {
  background: url(/public/assets/cross.png);
  background-repeat: no-repeat;
  background-size: contain;
}
header .logo {
  display: none;
}
header .primary-navigation {
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  inset: 0 0 0 0%;
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(12px);
  font-size: 2rem;
  transform: translateX(100%);
  transition: transform 350ms ease-out;
}
header .primary-navigation a {
  padding: 10px 20px;
  border: 2px solid transparent;
  border-radius: 84px;
  transition: 0.4s;
}
header .primary-navigation a:hover {
  padding: 10px 26px;
  border: 2px solid var(--black);
  border-radius: 84px;
}
header .primary-navigation .active {
  padding: 10px 26px;
  border: 2px solid var(--black);
  border-radius: 84px;
}
header .primary-navigation[data-visible=true] {
  transform: translateX(0%);
}
header .menu {
  width: 45px;
}

svg {
  fill: var(--black);
  transition: 0.4s;
}
svg:hover {
  fill: var(--light-brown);
}

main {
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: 40px 0px 120px 0px;
  gap: 133px;
  max-width: 18.875rem;
}
main .project-container {
  display: flex;
  flex-direction: column;
  gap: 108px;
}
main .project-container .project > * {
  margin-top: 25px;
}

.project-img {
  width: 100%;
  border-radius: 19px;
}

.skills {
  display: flex;
  justify-content: center;
  gap: 30px;
}
.skills .icon-text {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  width: 81px;
}
.skills .icon-text .skill-icon {
  width: 53px;
}
.skills span {
  font-weight: 600;
  text-align: center;
}

.skills + .skills {
  padding-top: 40px;
}

.hero-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 40px;
}
.hero-container .hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}
.hero-container .hero .profile-img {
  width: 272px;
  height: 272px;
}
.hero-container .hero .hero-text {
  display: flex;
  flex-direction: column;
  gap: 23px;
}
.hero-container .hero .hero-text .highlight {
  font-weight: 900;
}
.hero-container .arrow-img {
  z-index: -10;
  width: 50px;
  animation: wobble 1.7s;
  animation-iteration-count: infinite;
  animation-timing-function: ease;
}

.cite-container {
  text-align: center;
}
.cite-container .fancy-dots {
  display: inline-block;
  font-style: normal;
  font-size: 5rem;
  color: var(--light-brown);
  padding-bottom: 56px;
  line-height: 0.8;
}
.cite-container p {
  font-style: italic;
  padding-bottom: 56px;
  font-weight: 500;
}
.cite-container p span {
  font-weight: 300;
}

footer {
  background-color: var(--white);
  box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.25);
  padding: 33px 0px 33px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 32px;
}

@media (min-width: 44.6em) {
  body {
    background-image: url(/public/assets/backgroundmedium.png);
  }
  main {
    max-width: 37rem;
  }
  header {
    justify-content: space-between;
  }
  header .menu {
    display: none;
  }
  header .mobile-nav-toggle {
    display: none;
  }
  header .primary-navigation {
    position: unset;
    display: inline;
    background-color: transparent;
    inset: unset;
    font-size: 1.2rem;
    font-weight: 500;
    transform: unset;
  }
  header .primary-navigation a {
    padding: 10px 20px;
    border: 2px solid var(--white);
    border-radius: 84px;
    transition: 0.4s;
  }
  header .primary-navigation a + a {
    margin-left: 40px;
  }
  header .primary-navigation a:hover {
    padding: 10px 26px;
    border: 2px solid var(--black);
    border-radius: 84px;
  }
  .hero-container .profile-img {
    width: 309px;
    height: 309px;
  }
  .skills + .skills {
    padding-top: 0px;
  }
  .skills-no-wrap {
    display: flex;
    gap: 25px;
    justify-content: center;
  }
  footer {
    flex-direction: row;
    justify-content: center;
    gap: 130px;
  }
}
@media (min-width: 62.3em) {
  body {
    background-image: url(/public/assets/backgroundlarge.png);
  }
  main {
    max-width: 54.75rem;
    padding: 80px 0px 120px 0px;
  }
  header {
    justify-content: space-between;
  }
  header .logo {
    display: inline;
    width: 54px;
  }
  .hero-container .hero {
    flex-direction: row;
    align-items: center;
    gap: 20px;
  }
  .hero-container .hero .profile-img {
    order: 1;
    width: 309px;
    height: 309px;
  }
  .hero-container .hero .hero-text {
    order: 0;
  }
  .project-container {
    max-width: 37rem;
    margin: auto;
  }
}